*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}
html{
  scroll-behavior:smooth;
}

body {
  font-size:17px; 
}

a{
  text-decoration:none;
  color:white;
}

@media (max-width:768px){
  body{
    font-size:16px;
  }
}

@media (min-width:480px) and (max-width:768px){
  body{
  font-size:15px;
  }
}

@media (max-width:480px){
  body{
    font-size:14px;
  }
}





